import { Directive, ElementRef, OnInit, HostListener } from '@angular/core';

@Directive({
  selector: '[appDrag]'
})
export class DragDirective implements OnInit {
  constructor(public el: ElementRef) {
  }
  public isDown = false;

  public disX; // è®°å½é¼ æ ç¹å»äºä»¶çä½ç½® X

  public disY; // è®°å½é¼ æ ç¹å»äºä»¶çä½ç½® Y


  private totalOffsetX = 0; // è®°å½æ»åç§»é Xè½´
  private totalOffsetY = 0; // è®°å½æ»åç§»é Yè½´

  // ç¹å»äºä»¶
  @HostListener('mousedown', ['$event']) onMousedown(event) {
    this.isDown = true;
    this.disX = event.clientX;
    this.disY = event.clientY;
  }

  // çå¬documentç§»å¨äºä»¶äºä»¶
  @HostListener('document:mousemove', ['$event']) onMousemove(event) {
    // å¤æ­è¯¥åç´ æ¯å¦è¢«ç¹å»äºã
    if (this.isDown) {
      this.el.nativeElement.style.left = this.totalOffsetX + event.clientX - this.disX + 'px';
      this.el.nativeElement.style.top = this.totalOffsetY + event.clientY - this.disY + 'px';
    }
  }

  // çå¬documentç¦»å¼äºä»¶
  @HostListener('document:mouseup', ['$event']) onMouseup(event) {
    // åªç¨å½åç´ ç§»å¨è¿äºï¼ç¦»å¼å½æ°ä½æä¼è§¦åã
    if (this.isDown) {
      this.totalOffsetX += event.clientX - this.disX;
      this.totalOffsetY += event.clientY - this.disY;
      this.isDown = false;
    }
  }

  ngOnInit() {
    this.el.nativeElement.style.position = 'relative';
  }
}
